import React, { useState } from "react";
import { Steps, Button, Row, Col, Modal, Form } from "antd";
import moment from "moment";
import FormStepPayer from "./FormStepPayer";
import FormStepPayee from "./FormStepPayee";
import FormStepNaturePayment from "./FormStepNaturePayment";
import FormStepComment from "./FormStepComment";
import FormStepSummary from "./FormStepSummary";
import {
  WizardProcedureData,
  ThirdParty,
  BankAccount,
} from "../../utils/types";
import {
  createWorkflow,
  ACCOUNT_TYPE,
  PARTY_TYPE,
  DOCUMENT_TYPE,
  displaySuccessNotification,
  displayErrorNotification,
  WORKFLOW_DATE_FORMAT,
} from "../../utils";
import "./styles.less";

const { Step } = Steps;

const initialWizardData: WizardProcedureData = {
  address: "",
  amount: 0,
  bankAccount: "",
  beneficiary: "",
  beneficiaryAddress: "",
  beneficiaryBankAccount: "",
  beneficiaryBic: "",
  beneficiaryCountry: "",
  beneficiaryType: PARTY_TYPE.AUDITOR,
  bic: "",
  client: "",
  clientType: "",
  comment: "",
  country: "",
  currency: "",
  documentDate: moment(),
  documentReference: "",
  documentType: DOCUMENT_TYPE.CONTRACT,
  paymentDateLine: moment(),
  expenseType: "",
  upload: [],
};

const initialThirdParty: ThirdParty = {
  businessRegistrationId: "",
  country: "",
  fullAddress: "",
  fullName: "",
  id: "",
  lei: "",
  partyType: PARTY_TYPE.AUDITOR,
  taxIdentificationId: "",
  vatNumber: "",
  chartOfAccount: "",
  managementOffice: "",
};

const initialBankAccount: BankAccount = {
  accountNumber: "",
  accountType: ACCOUNT_TYPE.BENEFICIARY,
  balanceAmount: 0,
  balanceDate: "",
  bank: "",
  currency: "",
  internalReference: "",
  overdraftLimit: 0,
  owner: "",
};

type WizardNewProcedureProps = {
  visible: boolean;
  onClose: Function;
  loadWorkflows: Function;
};

const WizardNewProcedure = ({
  visible,
  onClose,
  loadWorkflows,
}: WizardNewProcedureProps) => {
  const [currentStep, setCurrentStep] = useState(0);
  const [isCreatingWorkflow, setIsCreatingWorkflow] = useState(false);
  const [selectedDebtor, setSelectedDebtor] = useState<ThirdParty>(
    initialThirdParty
  );
  const [selectedDebtorAccount, setSelectedDebtorAccount] = useState<
    BankAccount
  >(initialBankAccount);
  const [selectedBeneficiary, setSelectedBeneficiary] = useState<ThirdParty>(
    initialThirdParty
  );
  const [selectedBeneficiaryAccount, setSelectedBeneficiaryAccount] = useState<
    BankAccount
  >(initialBankAccount);
  const [wizardData, setWizardData] = useState<WizardProcedureData>(
    initialWizardData
  );
  const [documentIds, setDocumentIds] = useState<string[]>([]);
  const [form] = Form.useForm();

  const stepList = [
    {
      title: "Payer",
      content: (
        <FormStepPayer
          form={form}
          setPayer={setSelectedDebtor}
          setPayerAccount={setSelectedDebtorAccount}
        />
      ),
    },
    {
      title: "Payee",
      content: (
        <FormStepPayee
          form={form}
          setPayee={setSelectedBeneficiary}
          setPayeeAccount={setSelectedBeneficiaryAccount}
        />
      ),
    },
    {
      title: "Nature of payment",
      content: (
        <FormStepNaturePayment form={form} updateDocumentIds={setDocumentIds} />
      ),
    },
    {
      title: "Comment",
      content: <FormStepComment form={form} />,
    },
    {
      title: "Summary",
      content: <FormStepSummary formInputs={wizardData} />,
    },
  ];

  const next = () => {
    form
      .validateFields()
      .then((values) => {
        setWizardData({ ...wizardData, ...values });
        setCurrentStep(currentStep + 1);
      })
      .catch(() => {});
  };

  const prev = () => {
    setCurrentStep(currentStep - 1);
  };

  const handleOnOk = () => {
    onClose();
  };

  const submitWizard = () => {
    setIsCreatingWorkflow(true);
    createWorkflow({
      businessData: {
        amount: wizardData.amount,
        beneficiary: {
          account: { ...selectedBeneficiaryAccount },
          party: { ...selectedBeneficiary },
        },
        comment: wizardData.comment,
        currency: wizardData.currency,
        debtor: {
          account: { ...selectedDebtorAccount },
          party: { ...selectedDebtor },
        },
        documents: documentIds,
        dueDate: wizardData.paymentDateLine.format(WORKFLOW_DATE_FORMAT),
        expenseReference: wizardData.documentReference,
        expenseType: wizardData.expenseType,
        frequency: "ONCE",
        identifier: "",
        invoiceDate: wizardData.documentDate.format(WORKFLOW_DATE_FORMAT),
        process: "",
      },
      initiator: "john.stone@saltgate.com",
      processType: "EXPENSE_APPROBATION",
    })
      .then(() => {
        displaySuccessNotification("Workflow successfully created !");
        setIsCreatingWorkflow(false);
        form.resetFields();
        onClose();
        loadWorkflows();
      })
      .catch(() => {
        displayErrorNotification("Unable to create the workflow !");
        setIsCreatingWorkflow(false);
      });
  };

  const modalFooter = (
    <div className="wizardnewprocedure__steps-action">
      {currentStep > 0 && (
        <Button onClick={prev} shape="round" size="large">
          Previous
        </Button>
      )}
      {currentStep < stepList.length - 1 && (
        <Button type="primary" onClick={next} shape="round" size="large">
          Next
        </Button>
      )}
      {currentStep === stepList.length - 1 && (
        <Button
          type="primary"
          onClick={submitWizard}
          shape="round"
          size="large"
          loading={isCreatingWorkflow}
        >
          Done
        </Button>
      )}
    </div>
  );

  return (
    <Modal
      title="Initialize a payment process"
      visible={visible}
      onOk={handleOnOk}
      onCancel={handleOnOk}
      width="75%"
      className="wizardnewprocedure"
      footer={modalFooter}
    >
      <Row>
        <Col span={5}>
          <Steps current={currentStep} direction="vertical">
            {stepList.map((item) => (
              <Step key={item.title} title={item.title} />
            ))}
          </Steps>
        </Col>
        <Col span={19}>
          <div className="wizardnewprocedure__steps-content">
            {stepList[currentStep].content}
          </div>
        </Col>
      </Row>
    </Modal>
  );
};

export default WizardNewProcedure;
