import React, { useEffect, useState } from "react";
import { Form, Typography, Row, Col, Select, Input } from "antd";
import { FormInstance } from "antd/lib/form";
import {
  GUTTER,
  getThirdparties,
  displayErrorNotification,
  getThirdpartyBankAccounts,
} from "../../utils";
import { ThirdParty, BankAccount } from "../../utils/types";

const { Title } = Typography;
const { Option } = Select;

type FormStepPayeeProps = {
  form: FormInstance;
  setPayee: Function;
  setPayeeAccount: Function;
};

const FormStepPayee = ({
  form,
  setPayee,
  setPayeeAccount,
}: FormStepPayeeProps) => {
  const [thirdparties, setThirdparties] = useState<ThirdParty[]>([]);
  const [bankAccounts, setBankAccounts] = useState<BankAccount[]>([]);

  const handleOnChangeThirdParty = (id: string) => {
    const foundThirdparty = thirdparties.find((item) => item.id === id);

    if (foundThirdparty) {
      setPayee(foundThirdparty);
      form.setFieldsValue({
        beneficiaryType: foundThirdparty.partyType,
        beneficiaryAddress: foundThirdparty.fullAddress,
        beneficiaryCountry: foundThirdparty.country,
      });

      getThirdpartyBankAccounts(foundThirdparty.id)
        .then((res: BankAccount[]) => {
          setBankAccounts(res);
        })
        .catch(() => {
          displayErrorNotification("Unable to load bank accounts");
        });
    }
  };

  const handleOnChangeBankAccount = (accountNumber: string) => {
    const foundBankAccount = bankAccounts.find(
      (item) => item.accountNumber === accountNumber
    );

    if (foundBankAccount) {
      setPayeeAccount(foundBankAccount);
      form.setFieldsValue({
        beneficiaryBic: foundBankAccount.bank,
      });
    }
  };

  useEffect(() => {
    getThirdparties()
      .then((res: ThirdParty[]) => {
        setThirdparties(res);
      })
      .catch(() => {
        displayErrorNotification("Unable to load third parties");
      });
  }, []);

  return (
    <Form
      className="formsteppayee"
      name="formsteppayee"
      size="large"
      form={form}
    >
      <Title level={3}>Beneficiary Information</Title>
      <Row>
        <Col span={16} offset={4}>
          <Form.Item
            name="beneficiary"
            rules={[{ required: true, message: "Please select a third party" }]}
          >
            <Select
              placeholder="Select a third party *"
              onChange={(value: string) => {
                handleOnChangeThirdParty(value);
              }}
            >
              {thirdparties.map((thirdparty) => (
                <Option value={thirdparty.id} key={thirdparty.id}>
                  {thirdparty.fullName}
                </Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item
            name="beneficiaryType"
            rules={[
              {
                required: true,
              },
            ]}
          >
            <Input placeholder="Third party type *" />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={GUTTER.FORM}>
        <Col span={10} offset={4}>
          <Form.Item
            name="beneficiaryAddress"
            rules={[
              {
                required: true,
              },
            ]}
          >
            <Input placeholder="Address *" />
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item
            name="beneficiaryCountry"
            rules={[
              {
                required: true,
              },
            ]}
          >
            <Input placeholder="Country *" />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={GUTTER.FORM}>
        <Col span={10} offset={4}>
          <Form.Item
            name="beneficiaryBankAccount"
            rules={[
              { required: true, message: "Please select a bank account" },
            ]}
          >
            <Select
              placeholder="Select a bank account *"
              onChange={handleOnChangeBankAccount}
            >
              {bankAccounts.map((item) => (
                <Option value={item.accountNumber} key={item.accountNumber}>
                  {item.accountNumber}
                </Option>
              ))}
            </Select>
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item
            name="beneficiaryBic"
            rules={[
              {
                required: true,
              },
            ]}
          >
            <Input placeholder="Bank *" />
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
};

export default FormStepPayee;
