import React, { useEffect, useState } from "react";
import { Form, Typography, Row, Col, Select, Input, InputNumber } from "antd";
import { FormInstance } from "antd/lib/form";
import {
  GUTTER,
  getThirdparties,
  displayErrorNotification,
  getThirdpartyBankAccounts,
} from "../../utils";
import { ThirdParty, BankAccount } from "../../utils/types";

const { Title } = Typography;
const { Option } = Select;

type FormStepPayerProps = {
  form: FormInstance;
  setPayer: Function;
  setPayerAccount: Function;
};

const FormStepPayer = ({
  form,
  setPayer,
  setPayerAccount,
}: FormStepPayerProps) => {
  const [thirdparties, setThirdparties] = useState<ThirdParty[]>([]);
  const [bankAccounts, setBankAccounts] = useState<BankAccount[]>([]);

  useEffect(() => {
    getThirdparties()
      .then((res: ThirdParty[]) => {
        setThirdparties(res);
      })
      .catch(() => {
        displayErrorNotification("Unable to load third parties");
      });
  }, []);

  const handleOnChangeClient = (name: string) => {
    const foundThirdparty = thirdparties.find((item) => item.fullName === name);

    if (foundThirdparty) {
      setPayer(foundThirdparty);
      form.setFieldsValue({
        clientType: foundThirdparty.partyType,
        address: foundThirdparty.fullAddress,
        country: foundThirdparty.country,
      });

      getThirdpartyBankAccounts(foundThirdparty.id)
        .then((res: BankAccount[]) => {
          setBankAccounts(res);
        })
        .catch(() => {
          displayErrorNotification("Unable to load bank accounts");
        });
    }
  };

  const handleOnChangeBankAccount = (accountNumber: string) => {
    const foundBankAccount = bankAccounts.find(
      (item) => item.accountNumber === accountNumber
    );

    if (foundBankAccount) {
      form.setFieldsValue({
        bic: foundBankAccount.bank,
      });
      setPayerAccount(foundBankAccount);
    }
  };

  return (
    <Form
      className="formsteppayer"
      name="formsteppayer"
      size="large"
      form={form}
    >
      <Title level={3}>Payer Information</Title>
      <Row>
        <Col span={16} offset={4}>
          <Form.Item name="client" rules={[{ required: true }]}>
            <Select
              placeholder="Select a client *"
              onChange={(value: string) => {
                handleOnChangeClient(value);
              }}
            >
              {thirdparties.map((thirdparty) => (
                <Option value={thirdparty.fullName} key={thirdparty.id}>
                  {thirdparty.fullName}
                </Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item
            name="clientType"
            rules={[
              {
                required: true,
              },
            ]}
          >
            <Input placeholder="Client type *" />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={GUTTER.FORM}>
        <Col span={10} offset={4}>
          <Form.Item
            name="address"
            rules={[
              {
                required: true,
              },
            ]}
          >
            <Input placeholder="Address *" />
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item
            name="country"
            rules={[
              {
                required: true,
              },
            ]}
          >
            <Input placeholder="Country *" />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={GUTTER.FORM}>
        <Col span={10} offset={4}>
          <Form.Item
            name="bankAccount"
            rules={[
              { required: true, message: "Please select a bank account !" },
            ]}
          >
            <Select
              placeholder="Select a bank account *"
              onChange={handleOnChangeBankAccount}
            >
              {bankAccounts.map((item) => (
                <Option value={item.accountNumber} key={item.accountNumber}>
                  {item.accountNumber}
                </Option>
              ))}
            </Select>
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item
            name="bic"
            rules={[
              {
                required: true,
              },
            ]}
          >
            <Input placeholder="Bank *" />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={GUTTER.FORM}>
        <Col span={10} offset={4}>
          <Form.Item
            name="amount"
            rules={[
              {
                required: true,
              },
            ]}
          >
            <InputNumber placeholder="Amount *" />
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item
            name="currency"
            rules={[{ required: true, message: "Please select a currency" }]}
          >
            <Select placeholder="Select a currency *">
              <Option value="usd">U.S. Dollar</Option>
              <Option value="eur">Euro</Option>
              <Option value="yen">Yen</Option>
              <Option value="gbp">Great British Pound</Option>
              <Option value="cd">Canada Dollar</Option>
              <Option value="sf">Swiss Franc</Option>
            </Select>
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
};

export default FormStepPayer;
