import { notification } from "antd";
import { WorkflowData } from "./types";

const { protocol, hostname } = window.location;

const API_URL_PORT = "8080";

// const API_URL = `${protocol}//${hostname}:${API_URL_PORT}/api`;

const API_URL = `${protocol}//192.168.178.63:${API_URL_PORT}/api`;

const WORKFLOW_DATE_FORMAT = "YYYY-MM-DD";

enum GUTTER {
  FORM = 8,
}

enum ROUTES {
  LOGIN = "/",
  DASHBOARD = "/dashboard",
}

enum LANGUAGE {
  EN = "en",
  FR = "fr",
}

enum DASHBOARD_TAB {
  TRANSACTIONS = "TRANSACTIONS",
  ACCOUNTS = "ACCOUNTS",
  BENEFICIARY = "BENEFICIARY",
  REPORTS = "REPORTS",
}

enum ACCOUNT_TYPE {
  PAYMENT = "PAYMENT",
  BENEFICIARY = "BENEFICIARY",
}

enum PARTY_TYPE {
  CLIENT = "CLIENT",
  BENEFICIARY = "BENEFICIARY",
  PROVIDER = "PROVIDER",
  LEGISLATOR = "LEGISLATOR",
  AUDITOR = "AUDITOR",
}

enum DOCUMENT_TYPE {
  OTHER = "OTHER",
  SERVICE_BILLING = "SERVICE_BILLING",
  VENDOR_DOCUMENT = "VENDOR_DOCUMENT",
  PAYMENT_REQUEST = "PAYMENT_REQUEST",
  INVOICE = "INVOICE",
  PURCHASE_ORDER = "PURCHASE_ORDER",
  CONTRACT = "CONTRACT",
  LIABILITY_WAIVER = "LIABILITY_WAIVER",
}

const editHeader = (title: string) => {
  document.title = `${title} - Upya`;
};

const displaySuccessNotification = (title: string) => {
  notification.success({
    message: title,
    placement: "bottomRight",
  });
};

const displayErrorNotification = (title: string) => {
  notification.error({
    message: title,
    placement: "bottomRight",
  });
};

const createWorkflow = (process: WorkflowData) => {
  return fetch(`${API_URL}/workflow`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(process),
  }).then((res) => res.text());
};

const getThirdparties = () => {
  return fetch(`${API_URL}/thirdparty`).then((res) => res.json());
};

const getThirdpartyBankAccounts = (id: string) => {
  return fetch(`${API_URL}/account/thirdparty/${id}`).then((res) => res.json());
};

const uploadDocument = (fileData: FormData, details: string) => {
  return fetch(`${API_URL}/document?detailDto=${details}`, {
    method: "POST",
    body: fileData,
  }).then((res) => res.json());
};

const getWorkflows = () => {
  return fetch(`${API_URL}/workflow`).then((res) => res.json());
};

const getExpenseTypes = () => {
  return fetch(`${API_URL}/expense/type`).then((res) => res.json());
};

export {
  API_URL,
  ROUTES,
  LANGUAGE,
  DASHBOARD_TAB,
  GUTTER,
  ACCOUNT_TYPE,
  PARTY_TYPE,
  DOCUMENT_TYPE,
  WORKFLOW_DATE_FORMAT,
  editHeader,
  displaySuccessNotification,
  displayErrorNotification,
  createWorkflow,
  getThirdparties,
  getThirdpartyBankAccounts,
  uploadDocument,
  getWorkflows,
  getExpenseTypes,
};
