import React, { useEffect } from "react";
import { Row, Col, Card, Typography, Form, Input, Button } from "antd";
import { injectIntl, IntlShape } from "react-intl";
import { UserOutlined, LockOutlined } from "@ant-design/icons";
import { useHistory } from "react-router-dom";
import { editHeader, displaySuccessNotification, ROUTES } from "../../utils";
import logo from "../../assets/img/upya-logo.png";
import "./styles.less";

const { Title } = Typography;

type LoginProps = {
  intl: IntlShape;
};

const Login = ({ intl }: LoginProps) => {
  const history = useHistory();
  useEffect(() => {
    editHeader(intl.formatMessage({ id: "login.title" }));
  }, []);

  const handleOnFinish = (values: any) => {
    console.log("Login:", values);
    displaySuccessNotification(
      intl.formatMessage({ id: "login.form.success" })
    );
    setTimeout(() => {
      history.push(ROUTES.DASHBOARD);
    }, 2000);
  };

  return (
    <Row className="login-page">
      <Col span={12} className="login-page__left-column" />
      <Col span={12}>
        <div className="login-page__area">
          <ul className="login-page__circles">
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
          </ul>
          <Row
            align="middle"
            justify="space-around"
            className="login-page__login-form"
          >
            <Col span={12}>
              <div className="login-page__login-form__logo">
                <img src={logo} alt="Upya Logo" />
              </div>
              <Card
                title={
                  <Title level={2}>
                    {intl.formatMessage({ id: "login.title" })}
                  </Title>
                }
              >
                <Form size="large" onFinish={handleOnFinish}>
                  <Form.Item
                    name="username"
                    rules={[
                      {
                        required: true,
                        message: "Please input your username!",
                      },
                    ]}
                  >
                    <Input prefix={<UserOutlined />} placeholder="Username" />
                  </Form.Item>
                  <Form.Item
                    name="password"
                    rules={[
                      {
                        required: true,
                        message: "Please input your password!",
                      },
                    ]}
                  >
                    <Input.Password
                      prefix={<LockOutlined />}
                      placeholder="Password"
                    />
                  </Form.Item>
                  <div className="text-center">
                    <Button
                      size="large"
                      shape="round"
                      type="primary"
                      htmlType="submit"
                    >
                      Login
                    </Button>
                  </div>
                </Form>
              </Card>
            </Col>
          </Row>
        </div>
      </Col>
    </Row>
  );
};

export default injectIntl(Login);
