import React, { useState, useEffect } from "react";
import {
  Form,
  Typography,
  Row,
  Col,
  Select,
  Input,
  DatePicker,
  Upload,
  Button,
} from "antd";
import { UploadOutlined } from "@ant-design/icons";
import { FormInstance } from "antd/lib/form";
import { UploadFile, RcFile } from "antd/lib/upload/interface";
import moment, { Moment } from "moment";
import {
  GUTTER,
  API_URL,
  uploadDocument,
  displayErrorNotification,
  displaySuccessNotification,
  DOCUMENT_TYPE,
  WORKFLOW_DATE_FORMAT,
  getExpenseTypes,
} from "../../utils";
import { UploadedFile, SimpleGenericData } from "../../utils/types";

const { Title } = Typography;
const { Option } = Select;

type FormStepNaturePaymentProps = {
  form: FormInstance;
  updateDocumentIds: (ids: string[]) => void;
};

type CustomRcFile = {
  uid: string;
  name: string;
  status: string;
  url: string;
  size: number;
  type: string;
};

const FormStepNaturePayment = ({
  form,
  updateDocumentIds,
}: FormStepNaturePaymentProps) => {
  const [fileList, setFileList] = useState<UploadFile<CustomRcFile>[]>([]);
  const [types, setTypes] = useState<SimpleGenericData[]>([]);

  useEffect(() => {
    getExpenseTypes()
      .then((res: SimpleGenericData[]) => {
        setTypes(res);
      })
      .catch(() => {
        displayErrorNotification("Unable to expense types");
      });
  }, []);

  const normFile = (e: any) => {
    if (Array.isArray(e)) {
      return e;
    }

    return e && e.fileList;
  };

  const handleBeforeUpload = (file: RcFile) => {
    const { getFieldValue } = form;
    const formData = new FormData();
    const fieldDocumentDate: Moment = getFieldValue("documentDate").format(
      WORKFLOW_DATE_FORMAT
    );
    const details = `%7B"documentDate": "${fieldDocumentDate}", "documentReference": "${getFieldValue(
      "documentReference"
    )}", "documentType": "${getFieldValue("documentType")}"%7D`;

    formData.append("file", file);

    uploadDocument(formData, details)
      .then((res: UploadedFile) => {
        if (res.status) {
          displayErrorNotification("Upload of the file failed !");
        } else {
          updateDocumentIds([res.fileId]);
          setFileList([
            {
              uid: res.fileId,
              name: res.fileName,
              status: "done",
              url: `${API_URL}/document/${res.fileId}/download`,
              size: res.size,
              type: res.fileType,
            },
          ]);
          displaySuccessNotification("Your file was successfully uploaded !");
        }
      })
      .catch(() => {
        displayErrorNotification("Upload of the file failed !");
      });

    return false;
  };

  const documentDisabledDate = (current: Moment) => {
    return current && current > moment().endOf("day");
  };

  const paymentDisabledDate = (current: Moment) => {
    return current && current < moment().subtract(1, "day").endOf("day");
  };

  return (
    <Form
      className="formstepnaturepayment"
      name="formstepnaturepayment"
      size="large"
      form={form}
    >
      <Title level={3}>Nature of Payment</Title>
      <Row gutter={GUTTER.FORM}>
        <Col span={8} offset={4}>
          <Form.Item
            name="expenseType"
            rules={[
              { required: true, message: "Please select a payment purpose" },
            ]}
          >
            <Select placeholder="Select a purpose *">
              {types.map((type) => (
                <Option value={type.id} key={type.id}>
                  {type.description}
                </Option>
              ))}
            </Select>
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item
            name="paymentDateLine"
            rules={[
              { required: true, message: "Please select a payment date line" },
            ]}
          >
            <DatePicker
              placeholder="Payment date line *"
              disabledDate={paymentDisabledDate}
            />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={GUTTER.FORM}>
        <Col span={8} offset={4}>
          <Form.Item
            name="documentReference"
            rules={[
              {
                required: true,
                message: "Please input the reference of the document",
              },
            ]}
          >
            <Input placeholder="Document reference *" />
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item
            name="documentDate"
            rules={[
              {
                required: true,
                message: "Please select the date of the document",
              },
            ]}
          >
            <DatePicker
              placeholder="Document date *"
              disabledDate={documentDisabledDate}
            />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={GUTTER.FORM}>
        <Col span={16} offset={4}>
          <Form.Item
            name="documentType"
            rules={[
              {
                required: true,
                message: "Please select the type of the document",
              },
            ]}
          >
            <Select placeholder="Document type *">
              {Object.keys(DOCUMENT_TYPE).map((item) => (
                <Option value={item} key={item}>
                  {item}
                </Option>
              ))}
            </Select>
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={GUTTER.FORM}>
        <Col span={16} offset={4}>
          <Form.Item
            name="upload"
            getValueFromEvent={normFile}
            rules={[
              {
                required: true,
                message: "Please upload a PDF-file",
              },
            ]}
          >
            <Upload
              accept=".pdf, .PDF"
              action={`${API_URL}/document`}
              headers={{ authorization: "authorization-text" }}
              fileList={fileList}
              beforeUpload={handleBeforeUpload}
            >
              <Button>
                <UploadOutlined /> Click to upload
              </Button>
            </Upload>
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
};

export default FormStepNaturePayment;
