import React from "react";
import { Row, Divider, Dropdown, Col, Menu, Avatar } from "antd";
import { MenuOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";
import logo from "../../assets/img/upya-logo-small.png";
import profile from "../../assets/img/profile-51.jpg";
import { ROUTES } from "../../utils";
import "./styles.less";

const DashboardHeader = () => {
  const profileMenu = (
    <Menu>
      <Menu.Item>
        <Link to={ROUTES.LOGIN}>Logout</Link>
      </Menu.Item>
    </Menu>
  );

  return (
    <Row className="dashboardheader">
      <Col span={12} className="dashboardheader__left-header">
        <MenuOutlined className="dashboardheader__menu" />
        <Divider type="vertical" />
        <img src={logo} alt="Upya logo" />
      </Col>
      <Col span={12} className="text-right">
        <Dropdown overlay={profileMenu} trigger={["click"]}>
          <Avatar src={profile} size={50} />
        </Dropdown>
      </Col>
    </Row>
  );
};

export default DashboardHeader;
