import React, { useEffect, useState } from "react";
import { injectIntl, IntlShape } from "react-intl";
import { Row, Col, Tabs, Typography, Tooltip, Button, Table, Tag } from "antd";
import {
  CreditCardOutlined,
  AccountBookOutlined,
  AimOutlined,
  FileProtectOutlined,
  PlusOutlined,
} from "@ant-design/icons";
import moment from "moment";
import {
  editHeader,
  DASHBOARD_TAB,
  GUTTER,
  displayErrorNotification,
  getWorkflows,
} from "../../utils";
import DashboardHeader from "../../components/DashboardHeader";
import WizardNewProcedure from "../../components/WizardNewProcedure";
import { Workflow } from "../../utils/types";
import "./styles.less";

const { TabPane } = Tabs;
const { Title } = Typography;

type DashboardProps = {
  intl: IntlShape;
};

const Dashboard = ({ intl }: DashboardProps) => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [workflows, setWorkflows] = useState<Workflow[]>([]);

  const workflowColumns = [
    {
      title: "Process Type",
      dataIndex: "processType",
      key: "processType",
    },
    {
      title: "Triggered By",
      dataIndex: "initiator",
      key: "initiator",
    },
    {
      title: "Start date",
      dataIndex: "triggeredAt",
      key: "triggeredAt",
      render: (date: string) => moment(date).format("LLL"),
    },
    {
      title: "Status",
      dataIndex: "completedAt",
      key: "completedAt",
      render: (date: string) => {
        if (date) {
          return <Tag color="green">Completed</Tag>;
        } else {
          return <Tag color="orange">Pending</Tag>;
        }
      },
    },
    {
      title: "End date",
      dataIndex: "completedAt",
      key: "completedAt",
      render: (date: string) => {
        if (date) {
          return moment(date).format("LLL");
        }
      },
    },
  ];

  const loadWorkflows = () => {
    setIsLoading(true);
    getWorkflows()
      .then((res: Workflow[]) => {
        setWorkflows(res);
        setIsLoading(false);
      })
      .catch(() => {
        setIsLoading(false);
        displayErrorNotification("Unable to load workflows !");
      });
  };

  useEffect(() => {
    loadWorkflows();
  }, []);

  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const addButton = (
    <Tooltip title="Add a new payment" placement="left">
      <Button
        type="ghost"
        shape="circle"
        icon={<PlusOutlined />}
        size="large"
        onClick={openModal}
      />
    </Tooltip>
  );

  useEffect(() => {
    editHeader(intl.formatMessage({ id: "dashboard.title" }));
  }, []);

  return (
    <div className="dashboard-page">
      <DashboardHeader />
      <Row className="dashboard-page__content">
        <Col span={24}>
          <Tabs
            defaultActiveKey={DASHBOARD_TAB.TRANSACTIONS}
            tabBarExtraContent={addButton}
            tabBarGutter={GUTTER.FORM * 12}
          >
            <TabPane
              tab={
                <Title level={3}>
                  <CreditCardOutlined />
                  {intl.formatMessage({ id: "dashboard.tab.one" })}
                </Title>
              }
              key={DASHBOARD_TAB.TRANSACTIONS}
            >
              <Table
                dataSource={workflows}
                columns={workflowColumns}
                pagination={false}
                loading={isLoading}
                rowKey={(record: Workflow) => record.id}
              />
            </TabPane>
            <TabPane
              tab={
                <Title level={3}>
                  <AccountBookOutlined />
                  {intl.formatMessage({ id: "dashboard.tab.two" })}
                </Title>
              }
              key={DASHBOARD_TAB.ACCOUNTS}
            >
              Accounts
            </TabPane>
            <TabPane
              tab={
                <Title level={3}>
                  <AimOutlined />
                  {intl.formatMessage({ id: "dashboard.tab.three" })}
                </Title>
              }
              key={DASHBOARD_TAB.BENEFICIARY}
            >
              Beneficiary
            </TabPane>
            <TabPane
              tab={
                <Title level={3}>
                  <FileProtectOutlined />
                  {intl.formatMessage({ id: "dashboard.tab.four" })}
                </Title>
              }
              key={DASHBOARD_TAB.REPORTS}
            >
              Reports
            </TabPane>
          </Tabs>
        </Col>
      </Row>
      <WizardNewProcedure
        visible={isModalOpen}
        onClose={closeModal}
        loadWorkflows={loadWorkflows}
      />
    </div>
  );
};

export default injectIntl(Dashboard);
